@import "vars";

.course {
  padding: 190px 0 85px;
  position: relative;
  .text-hidden:not(.text-hidden--active) {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:1;
    -webkit-box-orient: vertical;
    cursor: pointer;
    transition: all 0.5s;
  }
  &__bg-left {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
  }
  &__bg-right {
    position: absolute;
    top: 100px;
    right: 0;
    pointer-events: none;
  }
  &__inner {
    max-width: 800px;
    position: relative;
    z-index: 2;
  }
  &__title {
    span {
      color: $orange;
      display: block;
    }
  }
  &__desc {
    font-size: 24px;
    font-weight: 600;
    line-height: 130%;
    color: $black;
    margin-top: 50px;
    p {
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  &__info {
    font-size: 24px;
    font-weight: 600;
    line-height: 130%;
    color: $black;
    margin-top: 50px;
  }
  &__items {
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 1fr;
    gap: 50px;
    margin: 70px auto 0;
  }
  &__item {
    background: $white;
    .js-show {
      &--active {
        .course__item-title {
          color: $orange;
        }
        .course__item-btn {
          background-image: url("../img/accordion-icon2.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto auto;
          border: 1.5px solid $orange;
        }
      }
    }
    &-show {
      background: linear-gradient(140.19deg,rgba(227,227,227,.7) 25.07%,rgba(238,238,238,0) 66.06%),#f5f5f5;
      box-shadow: 0 1px 4px rgb(0 0 0 / 11%), inset 0 2px 4px #fff;
      border-radius: 20px 20px 0 0;
      padding: 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
    }
    &-title {
      font-family: 'Montserrat';
      font-weight: 500;
      font-size: 1.75rem;
      line-height: 120%;
      color: #666;
    }
    &-btn {
      width: 40px;
      height: 40px;
      border: 1.5px solid #444;
      border-radius: 50%;
      background-image: url(../img/accordion-icon.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto auto;
      flex-shrink: 0;
      margin-left: 10px;
    }
    &-hidden {
      padding: 40px 20px;
      display: none;
    }
    &-info {
      font-weight: 600;
      font-size: 24px;
      line-height: 140%;
      color: $black;
    }
    &-subtitle {
      font-weight: 700;
      font-size: 24px;
      line-height: 140%;
      color: $black;
      margin: 30px 0;
      text-transform: uppercase;
    }
  }
  &__video {
    display: flex;
    justify-content: space-between;
    &-items {
      display: grid;
      grid-template-columns: 1fr;
      gap: 30px;
    }
    &-content {
      flex-shrink: 0;
      width: 30%;
      margin-right: 30px;
      iframe {
        width: 100%;
        height: 180px;
      }
    }
    &-desc {
      width: calc(100% - 30% - 30px);
    }
    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 140%;
      color: $black;
    }
    &-info {
      font-size: 24px;
      line-height: 140%;
      color: $black;
      margin-top: 15px;
    }
  }
  &__files {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }
  &__file {
    display: flex;
    justify-content: space-between;
    &-input {
      position: relative;
      width: 30%;
      margin-right: 30px;
      text-align: center;
      input {
        position: absolute;
        opacity: 0;
        width: 90px;
        height: 90px;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        cursor: pointer;
        z-index: 2;
      }
      label {
        width: 90px;
        height: 90px;
        img {
          width: 100%;
          height: auto;
        }
      }
    }
    &-desc {
      width: calc(100% - 30% - 20px);
    }
    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 140%;
      color: $black;
    }
    &-info {
      font-size: 24px;
      line-height: 140%;
      color: $black;
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 992px) {
  .course {
    padding: 100px 0 25px;
    &__bg-left {
      top: -50px;
      left: -50px;
    }
    &__bg-right {
      right: -100px;
    }
    &__inner {
      max-width: 100%;
    }
    &__title {
      text-align: center;
    }
    &__desc {
      margin-top: 40px;
    }
    &__info {
      margin-top: 20px;
      text-align: center;
    }
    &__items {
      max-width: 100%;
      margin: 20px 0 0 0;
    }
    &__video {
      &-content {
       iframe {
         height: 130px;
       }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .course {
    &__bg-left {
      top: -20px;
      left: -150px;
    }
    &__bg-right {
      right: -20px;
    }
    &__desc {
      font-size: 12px;
      p {
        &:not(:first-child) {
          margin-top: 10px;
        }
      }
    }
    &__info {
      font-size: 8px;
    }
    &__items {
      gap: 15px;
    }
    &__item {
      &-show {
        padding: 10px;
      }
      .js-show {
        &--active {
          .answers__item-btn {
            background-size: auto 6px;
          }
        }
      }
      &-title {
        font-size: 14px;
      }
      &-btn {
        width: 24px;
        height: 24px;
        background-size: auto 6px;
      }
      &-hidden {
        padding: 20px 10px;
      }
      &-desc {
        font-size: 14px;
      }
      &-subtitle {
        font-size: 12px;
        margin: 20px 0 10px;
      }
      &-info {
        font-size: 12px;
      }
    }
    &__video {
      &-items {
        gap: 15px;
      }
      &-content {
        margin-right: 20px;
        iframe {
          height: 70px;
        }
      }
      &-desc {
        width: calc(100% - 30% - 20px);
      }
      &-title {
        font-size: 12px;
      }
      &-info {
        font-size: 10px;
        margin-top: 10px;
      }
    }
    &__files {
      gap: 15px;
    }
    &__file {
      &-input {
        margin-right: 20px;
        input {
          width: 35px;
          height: 35px;
        }
        label {
          width: 35px;
          height: 35px;
        }
      }
      &-desc {
        width: calc(100% - 30% - 20px);
      }
      &-title {
        font-size: 12px;
      }
      &-info {
        font-size: 10px;
        margin-top: 10px;
      }
    }
  }
}