@import "vars";

.white-bg {
  background: $white2 !important;
}
.header {
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0px 1px 2px rgba(102, 102, 102, 0.07);
  backdrop-filter: blur(12.5px);
  padding: 10px 0;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 230px;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      color: $black;
      text-transform: uppercase;
      &:not(:first-child) {
        margin-left: 96px;
      }
    }
  }
  &__btn {
    font-weight: 800;
    font-size: 20px;
    line-height: 14px;
    color: $white;
    background: linear-gradient(165.65deg, rgba(208, 35, 35, 0.7) 24.32%, rgba(239, 12, 12, 0) 74.26%), #EF0C0C;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FC8989;
    border-radius: 7px;
    width: 150px;
    height: 50px;
    margin-right: 20px;
    display: none;
    &:hover {
      background: linear-gradient(165.65deg, rgba(145, 22, 22, 0.7) 24.32%, rgba(144, 22, 22, 0) 74.26%), #890909;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FC8989;
    }
  }
  &__user {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(165.65deg, rgba(227, 227, 227, 0.7) 24.32%, rgba(227, 227, 227, 0) 74.26%), #F5F5F5;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FFFFFF;
  }
  &__menu-btn {
    display: none;
    cursor: pointer;
    position: relative;
    z-index: 5;
    span {
      display: block;
      &:nth-child(1) {
        font-weight: 600;
        font-size: 24px;
        line-height: 14px;
        color: $black;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__inner {
      padding:0 15px;
    }
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 50px;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__menu-btn {
     display: flex;
      &--active {
        width: 30px;
        height: 28px;
        span {
          height: 2px;
          background: $black;
          &:nth-child(1) {
            display: none;
          }
          &:nth-child(2) {
            position: absolute;
            transform: rotate(-45deg);
            top: 50%;
            width: 100%
          }
          &:nth-child(3) {
            position: absolute;
            transform: rotate(45deg);
            top: 50%;
            width: 100%
          }
        }
      }
    }
    &__nav {
      position: fixed;
      z-index: 4;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100vh;
      padding: 150px 15px;
      background-color: $gray2;
      transition: all 0.5s;
      &--active {
        left: 0;
      }
    }
    &__list {
      display: block;
      text-align: center;
      li {
        &:not(:first-child) {
          margin: 25px 0 0 0;
        }
      }
    }
    &__btn {
      display: flex;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__menu-btn {
      &--active {
        width: 22px;
        height: 15px;
      }
      span {
        &:nth-child(1) {
          font-size: 12px;
        }
      }
    }
    &__btn {
      width: 104px;
      height: 30px;
      font-size: 12px;
      margin-right: 12px;
    }
    &__nav {
      padding: 100px 15px;
    }
    &__list {
      li {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
  }
}