@import "vars";

.modal {
  .modal-dialog {
    max-width: 700px;
  }
  .modal-content {
    padding: 90px 120px;
    background: $white;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FFFFFF;
    backdrop-filter: blur(50px);
  }
  .modal-header {
    border: none;
    padding: 0;
    justify-content: end;
  }
  .modal-body {
    margin-top: 50px;
    padding: 0;
    &__text {
      font-weight: 600;
      font-size: 14px;
      line-height: 130%;
      color: $black;
    }
    &__info {
      font-weight: 600;
      font-size: 14px;
      line-height: 130%;
      color: $black;
      margin-top: 56px;
    }
  }
  &__form {
    margin-top: 56px;
    &-group {
      &:not(:first-child) {
        margin-top: 10px;
      }
      input {
        width: 100%;
        height: 50px;
      }
    }
    &-btn {
      width: 100%;
      height: 60px;
      margin-top: 56px;
    }
  }
  &__check {
    margin-top: 56px;
  }
  &__close-btn {
    background: transparent;
  }
}

@media screen and (max-width: 992px) {
  .modal {
    .modal-content {
      padding: 40px 20px;
    }
    .modal-body {
      margin-top: 36px;
      &__info {
        margin-top: 36px;
      }
    }
    &__form {
      margin-top: 36px;
      &-btn {
        margin-top: 36px;
      }
    }
    &__check {
      margin-top: 36px;
    }
  }
}

@media screen and (max-width: 576px) {
  .modal {
    .modal-body {
      &__info {
        font-size: 12px;
      }
    }
    &__form {
      &-btn {
        height: 40px;
      }
    }
  }
}