@import "vars";

.footer {
  padding: 100px 0;
  background: $black;
  &__inner {
    display: flex;
    justify-content: space-between;
    padding: 0 100px;
  }
  &__left {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__link {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    color: $white2;
    display: block;
    &:not(:first-child) {
      margin-left: 50px;
    }
  }
  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
    color: $gray3;
    width: 100%;
    margin-top: 50px;
  }
  &__socials {
    display: flex;
    align-items: center;
  }
  &__social {
    width: 65px;
    height: 65px;
    background: #444444;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.7);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    &:not(:first-child) {
      margin-left: 35px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__inner {
      padding: 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__inner {
      align-items: center;
    }
    &__left {
      display: block;
    }
    &__link {
      &:not(:first-child) {
        margin:25px 0 0 0;
      }
    }
    &__text {
      margin-top: 25px;
    }
    &__social {
      width: 40px;
      height: 40px;
      svg {
        width: 20px;
        height: auto;
      }
      &:not(:first-child) {
        margin-left: 20px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
   padding: 20px 0;
    &__link {
      font-size: 12px;
      &:not(:first-child) {
        margin: 10px 0 0 0;
      }
    }
    &__text {
      font-size: 10px;
      margin-top: 10px;
    }
    &__social {
      width: 20px;
      height: 20px;
      svg {
        width: 10px;
      }
      &:not(:first-child) {
        margin-left: 15px;
      }
    }
  }
}