@import "vars";
.main {
  padding: 190px 0 85px 0;
  position: relative;
  &__bg-left {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
  }
  &__bg-right {
    position: absolute;
    top: 100px;
    right: 0;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    color: $black;
    br {
      display: none;
    }
  }
  &__subtitle {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 36px;
    line-height: 130%;
    color: $gray;
    max-width: 404px;
    margin-top: 50px;
    text-transform: uppercase;
    span {
      font-weight: 800;
      color: $orange;
    }
  }
  &__btn {
    width: 257px;
    height: 60px;
    font-weight: 800;
    font-size: 34px;
    line-height: 120%;
    background: linear-gradient(165.65deg, rgba(208, 35, 35, 0.7) 24.32%, rgba(239, 12, 12, 0) 74.26%), #EF0C0C;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FC8989;
    border-radius: 10px;
    color: $white;
    text-transform: uppercase;
    margin-top: 50px;
    transition: all 0.3s;
    &:hover {
      background: linear-gradient(165.65deg, rgba(145, 22, 22, 0.7) 24.32%, rgba(144, 22, 22, 0) 74.26%), #890909;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FC8989;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
  }
  &__img {
    width: 273px;
    margin: 0 auto;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__link {
    font-weight: 600;
    font-size: 34px;
    line-height: 130%;
    color: $black;
    background: linear-gradient(258.09deg, rgba(227, 227, 227, 0.14) 24.94%, rgba(227, 227, 227, 0) 38.83%), rgba(245, 245, 245, 0.35);
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(5px);
    border-radius: 15px;
    padding: 15px 30px;
    display: block;
    margin-left: auto;
    &:not(:first-child) {
      margin-top: 30px;
    }
  }
}
.help {
  padding: 85px 0;
  position: relative;
  z-index: 2;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  &__title {
    position: relative;
   span {
     position: relative;
     z-index: 2;
   }
    img {
      position: absolute;
      top: -25px;
      left: -117px;
    }
  }
  &__btn {
    background: transparent;
    position: fixed;
    z-index: 99;
    right: 15px;
    bottom: 15px;
    opacity: 0;
    transition: all 0.5s;
    &--active {
      opacity: 1;
    }
  }
  &-cards {
    margin-top: 100px;
  }
}
.program {
  padding: 85px 0;
  position: relative;
  &__bg {
    position: absolute;
    top: -250px;
    left: 0;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    position: relative;
    max-width: 580px;
    span {
      position: relative;
      z-index: 2;
    }
    img {
      position: absolute;
      top: -30px;
      right: 135px;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 100px;
    margin-top: 100px;
  }
  &__item {
    background: rgba(245, 245, 245, 0.6);
    box-shadow: 0px 1px 4px #FFAD4C, 0px 1px 15px rgba(242, 131, 0, 0.3);
    border-radius: 20px;
    &-content {
      padding: 40px 20px 0;
    }
    &-top {
      text-align: center;
      background: linear-gradient(94.09deg, rgba(227, 227, 227, 0.2) 25.08%, rgba(227, 227, 227, 0) 53.83%), #F5F5F5;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FFFFFF;
      padding: 20px;
      text-transform: uppercase;
      border-radius: 20px 20px 0px 0px;
    }
    &-title {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 700;
      font-size: 34px;
      line-height: 130%;
      color: $orange;
    }
    &-info {
      font-weight: 600;
      font-size: 18px;
      line-height: 140%;
      color: $black;
      margin-bottom: 70px;
    }
    &-text {
      font-weight: 600;
      font-size: 18px;
      line-height: 140%;
      color: $black;
      p {
        font-weight: 600;
        font-size: 18px;
        line-height: 140%;
        color: $black;
        margin-bottom: 20px;
      }
    }
    &-subtitle {
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: $orange;
      margin-bottom: 20px;
    }
    &-bottom {
      background: linear-gradient(94.09deg, rgba(227, 227, 227, 0.2) 25.08%, rgba(227, 227, 227, 0) 53.83%), #F5F5F5;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FFFFFF;
      border-radius:0 0 20px 20px;
      padding: 20px;
    }
  }
  &__nav {
    &:not(:first-child) {
      margin-bottom: 40px;
    }
    &-title {
      font-weight: 600;
      font-size: 18px;
      line-height: 140%;
      color: $black;
      text-transform: uppercase;
    }
    ul li {
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: $black;
      position: relative;
      padding-left: 15px;
      &:after {
        content: '';
        position: absolute;
        top: 10px;
        left: 0;
        width: 3px;
        height: 3px;
        background-color: $black;
        border-radius: 50%;
      }
    }
  }
  &__accordion {
    border-top: 1px solid $gray3;
    padding: 35px 0;
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
    }
    &-title {
      font-weight: 600;
      font-size: 18px;
      line-height: 120%;
      color: $black;
    }
    &-btn {
      border: 1.5px solid $black;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 15px;
      background-image: url("../img/accordion-icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto auto;
    }
    &-hidden {
      display: none;
      padding-top: 30px;
      &--active {
        display: block;
      }
    }
  }
  .js-show {
    &--active {
      .program__accordion-title {
        color: $orange;
      }
      .program__accordion-btn {
        border: 1.5px solid $orange;
        background-image: url("../img/accordion-icon2.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto auto;
        svg {
          display: none;
        }
      }
    }
  }
  &__price {
    display: flex;
    justify-content: center;
    align-items: center;
    &-old {
      font-family: 'Montserrat';
      font-weight: 700;
      font-size: 34px;
      line-height: 130%;
      color: $black;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -10px;
        transform: translate(0, -50%) rotate(-8.57deg);
        width: calc(100% + 10px);
        height: 4px;
        background: $orange;
      }
      span {
        font-size: 18px;
      }
    }
    &-new {
      font-family: 'Montserrat';
      font-weight: 700;
      font-size: 34px;
      line-height: 130%;
      color: $orange;
      margin-left: 38px;
      span {
        font-size: 18px;
      }
    }
  }
  &__buy-btn {
    width: 330px;
    height: 60px;
    margin: 37px auto 0;
  }

}
.reviews {
  position: relative;
  padding: 85px 0 85px;
  &__bg {
    position: absolute;
    top: 70%;
    left: 53%;
    transform: translate(-50%, -50%);
    pointer-events: none;
  }
  &__bg-left {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
  }
  &__title {
    position: relative;
    span {
      position: relative;
      z-index: 2;
    }
    img {
      position: absolute;
      top: 50%;
      left: -25px;
      transform: translate(0, -50%);
      pointer-events: none;
    }
  }
  &__sliders {
    margin: 70px 0 0 100px;
    position: relative;
    //&:after {
    //  content: '';
    //  position: absolute;
    //  z-index: 2;
    //  top: 0;
    //  left: -10000px;
    //  width: 10000px;
    //  height: 100%;
    //  background: $white;
    //}
  }
  .mySwiper {
    overflow: unset;
  }
  .swiper-slide {
    background: linear-gradient(140.19deg, rgba(227, 227, 227, 0.7) 25.07%, rgba(238, 238, 238, 0) 66.06%), #F5F5F5;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FFFFFF;
    padding: 40px;
    min-width: 610px;
    img {
      width: 100%;
      height: auto;
    }
  }
}
.consultation {
  padding: 85px 0;
  position: relative;
  z-index: 3;
  &__title {
    position: relative;
    span {
      position: relative;
      z-index: 2;
    }
    img {
      position: absolute;
      top: 50%;
      left: -85px;
      transform: translate(0, -50%);
      pointer-events: none;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 100px;
    margin-top: 70px;
  }
  &__item {
    &-content {
      background: rgba(245, 245, 245, 0.6);
      border-radius: 10px 10px 0px 0px;
    }
    &-show {
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FFFFFF;
      display: flex;
      justify-content: space-between;
      padding: 20px;
      border-radius: 10px 10px 0px 0px;
      cursor: pointer;
    }
    .js-show {
      &--active {
        .consultation__item-title {
          color: $orange;
        }
        .consultation__item-btn {
          background-image: url("../img/accordion-icon2.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto auto;
          border: 1.5px solid $orange;
        }
      }
    }
    &-title {
      font-family: 'Montserrat';
      font-weight: 700;
      font-size: 34px;
      line-height: 130%;
      color: $black;
      text-transform: uppercase;
    }
    &-btn {
      width: 40px;
      height: 40px;
      border: 1.5px solid $black;
      border-radius: 50%;
      background-image: url("../img/accordion-icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto auto;
      flex-shrink: 0;
      margin-left: 10px;
    }
    &-hidden {
      padding: 10px 20px 40px 20px;
      display: none;
      &:active {
        display: block;
      }
      h3 {
        font-weight: 600;
        font-size: 18px;
        line-height: 150%;
        text-transform: uppercase;
        color: $black;
      }
      ul {
        margin: 30px 0;
        li {
          font-weight: 600;
          font-size: 18px;
          line-height: 150%;
          color: $black;
          padding-left: 15px;
          position: relative;
          &:after {
            content: '';
            position: absolute;
            top: 10px;
            left: 0;
            width: 3px;
            height: 3px;
            border-radius: 50%;
            background: $black;
          }
        }
      }
    }
    &-cost {
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: $orange;
    }
    &-info {
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: $gray;
      margin-top: 5px;
    }
    &-record {
      width: 430px;
      height: 60px;
      margin: 38px auto 0;
    }
  }
}
