@media screen and (max-width: 1200px) {
  .main {
    &__bg-left {
      top: -50px;
      left: -50px;
    }
    &__bg-right {
      right: -240px;
    }
  }
  .program {
    &__content {
      gap: 50px;
    }
  }
  .consultation {
    &__items {
      gap: 50px;
    }
  }
  .education {
    &__bg-left {
      width: 500px;
      height: auto;
    }
    &__title {
      max-width: 395px;
      img {
        width: 120px;
        height: auto;
        right: -30px;
      }
    }
    &__sliders {
      margin: 70px 0 0 20px;
    }
    .swiper-slide {
      width: 600px;
    }
  }
  .social {
    margin: 25px 0 0;
  }
}

