        background-size: auto 3px;
      }
      .js-show {
        &--active {
          .program__accordion-btn {
            background-image: url("../img/accordion-icon2.svg");
            background-repeat: no-repeat;
            background-position: top 50% left 50%;
            background-size: auto 3px;
          }
        }
      }
    }
    &__price {
      &-old {
        font-size: 16px;
        &:after {
          height: 2px;
        }
        span {
          font-size: 10px;
        }
      }
      &-new {
       font-size: 16px;
        margin-left: 20px;
        span {
          font-size: 10px;
        }
      }
    }
    &__buy-btn {
      width: 190px;
      height: 40px;
      font-size: 16px;
      margin: 20px auto 0;
    }
  }
  .reviews {
    &__title {
      img {
        left: 0;
        width: 50px;
      }
    }
  }
  .consultation {
    &__title {
      img {
        width: 50px;
      }
    }
    &__items {
      gap: 30px;
    }
    &__item {
      &-show {
        padding: 10px;
      }
      .js-show {
        &--active {
          .consultation__item-btn {
            background-size: auto 6px;
          }
        }
      }
      &-title {
        font-size: 16px;
      }
      &-btn {
        width: 24px;
        height: 24px;
        background-size: auto 6px;
      }
      &-hidden {
        padding: 25px 10px 15px;
        h3 {
          font-size: 12px;
        }
        ul {
          margin: 15px 0;
          li {
            font-size: 12px;
            &:after {
              top: 7px;
            }
          }
        }
      }
      &-cost {
        font-size: 12px;
      }
      &-info {
        font-size: 12px;
      }
      &-record {
        width: 202px;
        height: 40px;
        margin-top: 15px;
      }
    }
  }
  .answers {
    &__title {
      img {
        width: 50px;
      }
    }
    &__items {
      gap: 15px;
    }
    &__item {
      &-show {
        padding: 10px;
      }
      .js-show {
        &--active {
          .answers__item-btn {
            background-size: auto 6px;
          }
        }
      }
      &-title {
        font-size: 14px;
      }
      &-btn {
        width: 24px;
        height: 24px;
        background-size: auto 6px;
      }
      &-hidden {
        padding: 10px;
      }
      &-desc {
        font-size: 14px;
      }
    }
  }
  .education {
    &__bg-left {
      width: 250px;
    }
  }
  .social {
    &__title {
      font-size: 11px;
    }
    &__message {
      &-link {
        width: 30px;
        height: 30px;
        svg {
          width: 15px;
        }
      }
    }
  }
}