@import "vars";
.display1 {
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 56px;
  line-height: 130%;
  text-transform: uppercase;
}
.display2 {
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 50px;
  line-height: 130%;
  text-transform: uppercase;
}
.help-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px 80px;
}
.help-card {
  text-align: center;
  padding: 30px;
  background: linear-gradient(140.19deg, rgba(227, 227, 227, 0.7) 25.07%, rgba(238, 238, 238, 0) 66.06%), #F5F5F5;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FFFFFF;
  border-radius: 20px;
  border: none;
  display: flex;
  flex-direction: column;
  &:hover {
    transform: translate(-10px, -10px);
  }
  &--big {
    grid-column: 1/3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 100px;
    flex-direction: unset;
    .help-card__text {
      margin-top: 0;
      max-width: 300px;
    }
    .help-card__img {
      margin-bottom: 0;
    }
  }
  &__text {
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: $black;
    margin-top: auto;
  }
  &__img {
    flex-shrink: 0;
    margin-bottom: 60px;
  }
}
.btn-gray {
  background: linear-gradient(140.19deg, rgba(227, 227, 227, 0.7) 25.07%, rgba(238, 238, 238, 0) 66.06%), #F5F5F5;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.11), inset 0px 2px 4px #FFFFFF;
  border-radius: 10px;
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 34px;
  line-height: 120%;
  text-transform: uppercase;
  color: $orange;
  &:hover {
    color: $white;
    background: $orange;
  }
}
.js {
  &--active {
    box-shadow: 0px 1px 4px #FFAD4C, 0px 1px 15px rgba(242, 131, 0, 0.3);
  }
}
.form {
  &-group {
    input {
      background: linear-gradient(196.75deg, rgba(227, 227, 227, 0.2) -5.59%, rgba(227, 227, 227, 0) 63.64%), #F5F5F5;
      box-shadow: inset 0px -4px 4px #E9E9E9, inset 0px 2px 4px #E9E9E9;
      border-radius: 7px;
      padding: 0 20px;
      border: none;
      font-weight: 500;
      font-size: 18px;
      line-height: 120%;
      color: $black;
      &::placeholder {
        color: $gray4;
      }
    }
    }
  }
.check {
  display: flex;
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    cursor: pointer;
    z-index: 2;
    &:checked+label {
      &:after {
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto auto;
      }
    }
  }
  label {
    font-size: 14px;
    line-height: 120%;
    color: $black;
    position: relative;
    padding-left: 40px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      background: linear-gradient(196.75deg, rgba(227, 227, 227, 0.2) -5.59%, rgba(227, 227, 227, 0) 63.64%), #F5F5F5;
      box-shadow: inset 0px -4px 4px #E9E9E9, inset 0px 2px 4px #E9E9E9;
      border-radius: 50%;
    }
    span {
      text-transform: uppercase;
    }
    a {
      color: $orange;
    }
  }
}

@media screen and (max-width: 1200px) {
  .display1 {
    font-size: 40px;
  }
  .display2 {
    font-size: 35px;
  }
  .help-cards {
   gap: 30px;
  }
  .help-card {
    padding: 15px;
    &--big {
      padding: 50px;
    }
  }
}

@media screen and (max-width: 992px) {
  .help-cards {
    grid-template-columns: 1fr 1fr;
  }
  .help-card {
    &--big {
      justify-content: center;
      .help-card__text {
        margin-left: 30px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .help-card {
    padding: 10px;
    &--big {
      padding: 35px 15px;
    }
  }
}

@media screen and (max-width: 576px) {
  .display1 {
    font-size: 20px;
  }
  .display2 {
    font-size: 16px;
  }
  .help-cards {
    gap: 20px;
  }
  .help-card {
    &--big {
      .help-card__text {
        margin-left: 10px;
      }
    }
    &__text {
      font-size: 10px;
    }
    &__img {
      width: 50px;
      height: 50px;
      margin: 0 auto 8px;
    }
  }
  .btn-gray {
    font-size: 16px;
    border-radius: 7px;
  }
  .form {
    &-group {
      input {
        height: 30px;
        font-size: 14px;
        padding: 0 10px;
      }
    }
  }
  .check {
    input {
      &:checked+label {
        &:after {
          background-size: 9px 9px;
        }
      }
    }
    label {
      font-size: 12px;
      padding-left: 30px;
      &:after {
        width: 15px;
        height: 15px;
      }
    }
  }
}